import * as React from "react";
import Dialog from "@mui/material/Dialog";
import AppBar from "@mui/material/AppBar";
import Toolbar from "@mui/material/Toolbar";
import IconButton from "@mui/material/IconButton";
import Typography from "@mui/material/Typography";
// import CloseIcon from "@mui/icons-material/Close";
import Slide from "@mui/material/Slide";
import { IoClose } from "react-icons/io5";
import styled from "styled-components";
import { useAddStudent } from "../../../Context/AddStudent";
import { TiArrowBack } from "react-icons/ti";

const AddIndividual = React.lazy(() => import("./AddIndividual"));
const ImportDocument = React.lazy(() => import("./ImportDocument"));
const AddFromAnother = React.lazy(() => import("../AddStudent/AddFromAnother"));
const ViewStudentOtherFromImport = React.lazy(() =>
  import("../AddStudent/ViewStudentOtherFromImport")
);

const ActionPanelForImport = React.lazy(() => import("./ActionPanelForImport"));

const Transition = React.forwardRef(function Transition(props, ref) {
  return <Slide direction="up" ref={ref} {...props} />;
});

export default function ImportStudents() {
  const students = useAddStudent();
  const handleClose = () => {
    students.handleOpen(false);
  };

  return (
    <div>
      <Dialog
        fullScreen
        open={students.openBox}
        onClose={handleClose}
        TransitionComponent={Transition}
      >
        {/**
         * Top Tools Bar
         */}
        <ToolsBar handleClose={handleClose} />

        <ImportPanel>
          {!students.isImport &&
          !students.isIndividual &&
          !students.isImportOther &&
          !students.isViewOther ? (
            <ActionPanelForImport />
          ) : (
            <React.Fragment>
              {students.isImport && <ImportDocument />}
              {students.isIndividual && <AddIndividual />}
              {students.isImportOther && <AddFromAnother />}
              {students.isViewOther && <ViewStudentOtherFromImport />}
            </React.Fragment>
          )}
        </ImportPanel>
      </Dialog>
    </div>
  );
}

function ToolsBar() {
  const students = useAddStudent();
  return (
    <AppBar
      sx={{
        position: "relative",
      }}
    >
      <Toolbar>
        <Icon>
          {students.isBack && (
            <IconButton
              edge="end"
              color="inherit"
              onClick={() => students.handleBack()}
              aria-label="close"
            >
              <TiArrowBack />
            </IconButton>
          )}
        </Icon>
        <Typography
          sx={{
            ml: 2,
            flex: 1,
          }}
          variant="h6"
          component="div"
        >
          Add Students
        </Typography>
        {/* <Button autoFocus color="inherit" onClick={handleClose}>
       save
      </Button> */}
        <p
          className="courseTitle"
          style={{
            marginBottom: "0",
            marginRight: "2rem",
          }}
        >
          {students.classInfo.courseName +
            " - Term: " +
            students.classInfo.term}
        </p>
        <IconButton
          edge="start"
          color="inherit"
          onClick={() => students.handleOpen(false)}
          aria-label="close"
        >
          <IoClose />
        </IconButton>
      </Toolbar>
    </AppBar>
  );
}

const ImportPanel = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  height: 100%;
  margin-top: 0.5rem;
`;

const Icon = styled.div`
  width: 45px;
  height: 45px;
  display: flex;
  align-items: center;
  justify-content: center;
`;
